import { useState } from 'react';
import { faqData } from '../components/Data';

function Faq() {
	const [opened, setOpened] = useState([0]);
	const openAccord = (i) => {
		if (opened.includes(i)) {
			setOpened((prev) => [...prev].filter((el) => el !== i));
		} else {
			setOpened((prev) => [...prev, i]);
		}
	};
	const openAccordAll = () => {
		setOpened(
			Array(faqData.length)
				.fill()
				.map((v, i) => i),
		);
	};
	const closeAccordAll = () => {
		setOpened([]);
	};

	return (
		<div className="content faq">
			<div className="sort">
				<h2 className="title">자주하는질문</h2>
				<div>
					<button className="active">
						<h3>전체</h3>
					</button>
					<button>
						<h3>시장</h3>
					</button>
					<button>
						<h3>주변안내</h3>
					</button>
					<button>
						<h3>이용관리</h3>
					</button>
				</div>
			</div>
			<div className="faq-list">
				<div className="title">
					<h2>전체</h2>
					<div className="btns">
						<button onClick={openAccordAll}>모두열기</button>
						<button onClick={closeAccordAll}>모두닫기</button>
					</div>
				</div>
				<ul className="accord-wrap">
					{faqData.map((q, i) => (
						<li key={i} className={opened.includes(i) ? 'accord-panel active' : 'accord-panel'}>
							<button
								className="accord-title"
								onClick={() => {
									openAccord(i);
								}}
							>
								<h3>Q. {q.question}</h3>
							</button>
							<div className="accord-content">
								<p>A. {q.answer}</p>
							</div>
						</li>
					))}
				</ul>
			</div>
		</div>
	);
}

export default Faq;
