import { trevelData } from '../components/Data';

function Around() {
	return (
		<div className="content around">
			{trevelData.map((trevel, i) => (
				<button key={i} onClick={null}>
					<figure>
						<img src={`${process.env.PUBLIC_URL}${trevel.thumb}`} alt="" />
						<figcaption>{trevel.caption}</figcaption>
					</figure>
					<h3>{trevel.title}</h3>
					<p className="text">{trevel.content}</p>
					<p className="source">{trevel.source}</p>
				</button>
			))}
		</div>
	);
}

export default Around;
